import React from 'react';
import Media from 'react-media';
import style from './second-screen.module.css';
import imgPortal from '../../img/infoImg/portal.png';
import imgPortalW from '../../img/infoImg/portal.webp';
import Header from './header/header';
import Information from './Information/information';
import NavigationServices from '../navigationServices/navigationServices';

const SectionTwo = ({ onOpenModal }) => (
  <section id="Portal" name="Portal" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={1} />
          </div>
        )
      }
    </Media>
    <Information onOpenModal={onOpenModal} />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <picture>
            <source srcSet={imgPortalW} type="image/webp" />
            <source srcSet={imgPortal} type="image/png" />
            <img className={style.img} src={imgPortal} alt="Portal" />
          </picture>
        )
      }
    </Media>
  </section>
);

export default SectionTwo;
